<template>
  <div class="row">
    <form role="form">
      <div class="col-md-2">
        <input type="text" class="form-control" placeholder="Title" v-model="formSearch.title">
      </div>

      <div class="col-md-2">
        <select class="form-control" v-model="formSearch.type" @change.prevent="handleSubmitFilter">
          <option value="">---Select Type---</option>
          <option v-bind:value="1">PreLanding</option>
          <option v-bind:value="2">Landing</option>
        </select>
      </div>
      <div class="col-md-2">
        <select class="form-control" v-model="formSearch.status">
          <option value="">---Select Status---</option>
          <option v-bind:value="1">Active</option>
          <option v-bind:value="3">Inactive</option>
        </select>
      </div>

      <div class="col-md-offset-8">
        <button type="submit" class="btn blue" @click.prevent="handleSubmitFilter">Search</button>
      </div>
    </form>

  </div>
</template>

<script>

export default {
  name: "FormSearch",
  data() {
    return {
      formSearch: {
        title: '',
        type: '',
        status: '',
      }
    }
  },
  methods: {
    handleSubmitFilter() {
      this.$emit('update:title', this.formSearch.title);
      this.$emit('update:type', this.formSearch.type);
      this.$emit('update:status', this.formSearch.status);
      this.$emit('completed', {});
    }
  }
}
</script>

