import configs from "@/helpers/configs";
import {authHeader} from "@/helpers/auth-header";
import axios from 'axios';

export const offerSourceService = {
    createOrUpdateOfferSource,
    getOfferSource,
    getListOfferSource,
    deleteOfferSource
}

function createOrUpdateOfferSource(offerSource, sourceId) {
    let url = configs.apiUrl + 'offer-source/create'
    let bodyFormData = new FormData();
    if (sourceId != null) {
        url = configs.apiUrl + 'offer-source/update'
        console.log("source id...", sourceId)
        bodyFormData.append("id", sourceId)
    }
    bodyFormData.append("title", offerSource.title);
    return axios({
        method: 'POST',
        url: url,
        headers: authHeader(),
        data: bodyFormData

    }).then(response => {
        console.log("create offersource response:")
        console.log(response.data)
        return response
    });
}

function getOfferSource(sourceId) {
    return axios({
        method: 'GET',
        url: configs.apiUrl + "offer-source/get?id=" + sourceId,
        headers: authHeader(),
        data: {}
    })
}

function getListOfferSource(query) {
    return axios({
        method: "GET",
        url: configs.apiUrl + "offer-source/list?",
        headers: authHeader(),
        params: query
    }).then(response => {
        return response
    })
}

function deleteOfferSource(argsId) {
    let bodyFormData = new FormData();
    bodyFormData.append("id", argsId);
    return axios({
        method: 'POST',
        url: configs.apiUrl + "offer-source/delete?",
        headers: authHeader(),
        data: bodyFormData

    }).then(response => {
        console.log("delete landing response:")
        console.log(response.data)
        return response
    });
}
