import Vue from 'vue'
import Router from 'vue-router'

import LoginPage from "@/views/auth/Login";
import ConfirmRegister from "@/views/auth/ConfirmRegister";
import RegisterPage from "@/views/auth/Register";
import ForgotPage from "@/views/auth/Forgot";
import ProfilePage from "@/views/users/Profile";

import Dashboard from "@/views/Dashboard";

Vue.use(Router);

import campaignRouter from "@/router/modules/campaign";
import landingRouter from "@/router/modules/landing";
import offerSourceRouter from "@/router/modules/offer_source";

const routes = [
    {
        path: '/dashboard', name: 'dashboard', component: Dashboard, meta: {
            title: 'Dashboard'
        }
    },
    {
        path: '/profile', name: "profile", component: ProfilePage, meta: {
            title: 'Profile'
        }
    },
    {
        path: '/login', name: "login", component: LoginPage, meta: {layout: 'user'},
    },
    {
        path: '/register', name: "register", component: RegisterPage, meta: {layout: 'user'},
    },
    {
        path: '/confirm-regis', name: "confirm-regis", component: ConfirmRegister, meta: {layout: 'blank'},
    },
    {
        path: '/forgot/:token?', name: "forgot", component: ForgotPage, meta: {layout: 'user'},
    },
    campaignRouter,
    landingRouter,
    offerSourceRouter
]


const router = new Router({
    mode: 'history',
    base: '/',
    routes: routes
})


router.beforeEach((to, from, next) => {
    const publicPages = ['/login', '/register', '/forgot', '/confirm-regis'];
    //const publicPages = ['/confirm-regis'];
    const authRequired = !publicPages.includes(to.path);
    const user = localStorage.getItem('user');
    if (authRequired && !user) {
        return next('/login');
    }
    if (user !== null) {
        const expiresIn = user["expiresIn"]
        // check expiresIn time token
        if (Math.round(+new Date() / 1000) > expiresIn) {
            return next('/login');
        }
    }
    next();
})

export default router
