import configs from "@/helpers/configs";
import {authHeader} from "@/helpers/auth-header";
import axios from 'axios';

export const landingService = {
    createOrUpdateLanding,
    getLanding,
    getListLanding,
    deleteLanding
}

function createOrUpdateLanding(landing, landingId) {
    let url = configs.apiUrl + 'landing/create'
    let bodyFormData = new FormData();
    if (landingId != null) {
        url = configs.apiUrl + 'landing/update'
        bodyFormData.append("id", landingId)
    }
    bodyFormData.append("title", landing.title);
    bodyFormData.append("type", landing.type);
    bodyFormData.append("url", landing.url);
    bodyFormData.append("status", landing.status);
    return axios({
        method: 'POST',
        url: url,
        headers: authHeader(),
        data: bodyFormData

    }).then(response => {
        console.log("create landing response:")
        console.log(response.data)
        return response
    });
}

function getLanding(landingId) {
    return axios({
        method: 'GET',
        mode: 'no-cors',
        url: configs.apiUrl + "landing/get?id=" + landingId,
        headers: authHeader(),
        data: {}
    })
}

function getListLanding(query) {
    console.log("query", query)
    return axios({
        method: "GET",
        url: configs.apiUrl + "landing/list?",
        headers: authHeader(),
        params: query
    }).then(response => {
        return response
    })
}

function deleteLanding(argsId) {
    let bodyFormData = new FormData();
    bodyFormData.append("id", argsId);
    return axios({
        method: 'POST',
        url: configs.apiUrl + "landing/delete?",
        headers: authHeader(),
        data: bodyFormData

    }).then(response => {
        console.log("delete landing response:")
        console.log(response.data)
        return response
    });
}
